/* eslint-disable react/prop-types */
/* eslint-disable jsx-a11y/href-no-hash */
/* eslint-disable jsx-a11y/label-has-for */
import React, { Component } from 'react';
import { dripForm, dripFormGroup } from '../../../../src/';
import { Layout, Button, Code } from '../../components/';
import {
  Input,
  FieldGroup,
  Textarea,
  Select,
  Checkbox,
  Radio,
} from '../../fields/';


const NestFieldsForm = dripForm({
  validations: {
    'teams.*.name': {
      required: true,
      max: 30,
    },
    'teams.*.members.*': {
      max: 30,
    },
  },
})(({
  handlers,
  fields,
  meta: { invalid, pristine },
}) => (
  <form onSubmit={handlers.onSubmit}>
    {fields.map('teams.*', (teamName, teamIndex) => (
      <div key={teamName}>
        <label htmlFor={teamName}>Team name #{teamIndex + 1}</label>
        <Input
          type="text"
          name={`${teamName}.name`}
          label="Team name"
        />

        <div>
          <FieldGroup name={`${teamName}.scope`}>
            <Radio value="private">Private</Radio>
            <Radio value="public">Public</Radio>
          </FieldGroup>
        </div>

        {fields.map(`${teamName}.members.*`, memberName => (
          <div key={memberName}>
            <label>Member name</label>
            <Input
              type="text"
              name={memberName}
              label="Member name"
            />
            <Button
              type="button"
              onClick={() => fields.remove(memberName)}
            >
              Delete
            </Button>
          </div>
        ))}

        <Button
          type="button"
          onClick={() => fields.push(`${teamName}.members`, '')}
        >
          Add member
        </Button>

        {' '}

        <Button
          type="button"
          onClick={() => fields.remove(teamName)}
        >
          Delete
        </Button>
      </div>
    ))}

    <Button
      type="button"
      onClick={() => fields.push('teams', {
        name: '',
        scope: 'private',
        members: [],
      })}
    >
      Add team
    </Button>

    {' '}

    <Button
      onClick={handlers.onSubmit}
      disabled={invalid || pristine}
    >
      Send message
    </Button>
  </form>
));


export default class NestFieldsFormExample extends Component {
  state = {
    values: {
      teams: [
        { name: 'Initial Team', scope: 'private', members: [] },
      ],
    },
  };

  render() {
    const { location } = this.props;
    const { values } = this.state;

    return (
      <Layout
        title="Nest Fields"
        location={location}
      >
        <style jsx>{`
          :global(form > div) {
            margin-bottom: 1em;
          }

          :global(form > div > label) {
            display: block;
            margin: 0 0 0.2em;
            font-weight: bold;
          }
        `}</style>

        <p>
          todo...
        </p>

        <hr />

        <h3>Example:</h3>
        <NestFieldsForm
          values={values}
          onChange={v => this.setState({ values: v })}
          onValidSubmit={(v) => {
            alert('See console');
            console.log(v);
          }}
        />
        <hr />

        <h3>Values:</h3>
        <Code language="javascript">{JSON.stringify(values, null, 2)}</Code>
        <hr />

        <h3>Sample Code:</h3>
        <Code language="javascript">{`import React from 'react';
import { dripForm } from 'react-drip-form';
`}</Code>
      </Layout>
    );
  }
}



